/* eslint-disable camelcase */
import React, { useState } from 'react';
import { connect } from 'react-redux';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  CustomInput,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import IntlMessages from '../../../../helpers/IntlMessages';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import { adminRoot } from '../../../../constants/defaultValues';
import StateButton from '../../../../components/StateButton';
import instance from '../../../../helpers/axiosInstance';
import { setProfileData } from '../../../../helpers/Utils';

const addLeadingZero = (input) => {
  return input < 10 ? `0${input}` : input;
};

const Education = ({ match, profileData }) => {
  const {
    id,
    degree_type,
    education_institution,
    field_of_study,
    month_of_graduation,
    year_of_graduation,
  } = profileData;

  const [editEducation, setEditEducation] = useState(false);
  const [noFormalEducation, setNoFormalEducation] = useState(false);
  const [currentlyStudyHere, setCurrentlyStudyHere] = useState(false);

  const [startDate, setStartDate] = useState(
    new Date(year_of_graduation, month_of_graduation - 1)
  );

  const [formValues, setFormValues] = useState({
    degree_type,
    education_institution,
    field_of_study,
    month_of_graduation,
    year_of_graduation,
  });

  const handleChange = (event, key) => {
    setFormValues({
      ...formValues,
      [key]: event.target.value,
    });
  };

  const handleSubmitEducation = (newData) => {
    return new Promise((resolve, reject) => {
      const updateProfile = async () => {
        const response = await instance({
          method: 'POST',
          url: '/updateProfile',
          data: { id, ...newData },
        });

        const { data } = response;

        if (data.success) {
          setProfileData({ ...profileData, ...newData });
          resolve('Profile updated successfully!');
        } else {
          reject('Failed to update profile!');
        }
      };

      updateProfile();
    });
  };

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.education" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row className="justify-content-center">
        <Colxx xxs="12" md="8">
          <Card className="mb-4">
            <div className="position-absolute card-top-buttons">
              <Button
                outline
                color="primary"
                onClick={() => {
                  setEditEducation(!editEducation);
                }}
                className="icon-button"
              >
                <i className="simple-icon-pencil" />
              </Button>
            </div>

            <CardBody>
              <CardTitle>
                <h2 className="lead">
                  <IntlMessages id="user.update-education" />
                </h2>
                <p className="mt-2">
                  <IntlMessages id="user.update-education-desc" />
                </p>
              </CardTitle>

              <Separator className="mb-3" />

              <FormGroup>
                <CustomInput
                  type="checkbox"
                  id="formal-education"
                  checked={noFormalEducation}
                  onChange={() => {
                    setNoFormalEducation(!noFormalEducation);
                    if (noFormalEducation) {
                      setFormValues({
                        ...formValues,
                        degree_type: '',
                        education_institution: '',
                      });
                    }
                  }}
                  disabled={!editEducation}
                  label="I don't have Formal Education."
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.degree-type" />
                </Label>
                <Input
                  type="text"
                  name="degree-type"
                  placeholder="e.g. B. Tech in Computer Science"
                  value={formValues.degree_type}
                  onChange={(event) => {
                    handleChange(event, 'degree_type');
                  }}
                  disabled={!editEducation || noFormalEducation}
                />
              </FormGroup>

              <FormGroup className="mb-5">
                <Label>
                  <IntlMessages id="forms.select-institution" />
                </Label>
                <Input
                  type="text"
                  name="select-institution"
                  placeholder="e.g Vellore Institute of Technology"
                  value={formValues.education_institution}
                  onChange={(event) => {
                    handleChange(event, 'education_institution');
                  }}
                  disabled={!editEducation || noFormalEducation}
                />
              </FormGroup>

              <Separator className="mt-2 mb-2" />

              <FormGroup>
                <CustomInput
                  type="checkbox"
                  id="curently-study-here"
                  checked={currentlyStudyHere}
                  onChange={() => {
                    setCurrentlyStudyHere(!currentlyStudyHere);
                    if (currentlyStudyHere) {
                      setFormValues({
                        ...formValues,
                        month_of_graduation: '',
                        year_of_graduation: '',
                      });
                    }
                  }}
                  disabled={!editEducation}
                  label="I currently study here."
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.field-of-study" />
                </Label>
                <Input
                  type="text"
                  name="field-of-study"
                  placeholder="e.g. Software Engineering"
                  value={formValues.field_of_study}
                  onChange={(event) => {
                    handleChange(event, 'field_of_study');
                  }}
                  disabled={!editEducation}
                />
              </FormGroup>

              <Row>
                <Colxx>
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.month-of-graduation" />
                    </Label>
                    {editEducation && !currentlyStudyHere ? (
                      <DatePicker
                        selected={startDate}
                        onChange={(date) => {
                          setStartDate(date);
                          const event = {
                            target: {
                              value: addLeadingZero(date.getMonth() + 1),
                            },
                          };
                          handleChange(event, 'month_of_graduation');
                        }}
                        dateFormat="MM"
                        showMonthYearPicker
                      />
                    ) : (
                      <Input
                        type="text"
                        name="month"
                        placeholder="e.g. 04"
                        value={formValues.month_of_graduation}
                        disabled={!editEducation || currentlyStudyHere}
                      />
                    )}
                  </FormGroup>
                </Colxx>

                <Colxx>
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.year-of-graduation" />
                    </Label>

                    {editEducation && !currentlyStudyHere ? (
                      <DatePicker
                        selected={startDate}
                        onChange={(date) => {
                          setStartDate(date);
                          const event = {
                            target: {
                              value: JSON.stringify(date.getFullYear()),
                            },
                          };
                          handleChange(event, 'year_of_graduation');
                        }}
                        showYearPicker
                        dateFormat="yyyy"
                      />
                    ) : (
                      <Input
                        type="text"
                        name="year"
                        placeholder="e.g. 2021"
                        value={formValues.year_of_graduation}
                        disabled={!editEducation || currentlyStudyHere}
                      />
                    )}
                  </FormGroup>
                </Colxx>
              </Row>

              {editEducation && (
                <FormGroup className="text-center">
                  <StateButton
                    id="submitAndNextButton"
                    color="primary"
                    onClick={() => handleSubmitEducation(formValues)}
                    next={() => {
                      setEditEducation(false);
                    }}
                  >
                    <IntlMessages id="forms.submit" />
                  </StateButton>
                </FormGroup>
              )}
            </CardBody>
          </Card>
        </Colxx>
      </Row>
      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              disabled={editEducation}
              href={`${adminRoot}/profile/about`}
            >
              <IntlMessages id="forms.previous" />
            </Button>

            <Button
              color="primary"
              className="m-3"
              disabled={editEducation}
              href={`${adminRoot}/profile/experience`}
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ history, authUser }) => {
  const { profileData } = authUser;
  return { profileData, history };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(Education);
